@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,700;1,400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=PT+Sans:ital,wght@0,400;0,700;1,400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&display=swap');

@import 'normalize.scss';
@import './helpers/helpers';

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  border: 0;
  // todo: add-boxshadow
  // outline: none;
}

*:focus {
  position: relative;
  z-index: 1000;
  outline: none;
  // box-shadow: 0 0 5px 3px #b043bd;
}

::selection {
  background: lightblue;
}

html,
body {
  // scroll-behavior: smooth;
  // ?
  // height: 100%;
}

.container {
  max-width: 980px;
  padding: 0 15px;
  margin: 0 auto;
}

.outer {
  padding: 0 15px;
}

a {
  display: block;
  text-decoration: none;

  color: #0066c0;

  // &:hover {
  //   color: blue;
  //   text-decoration: underline;
  // }
}

button {
  display: block;
}

body {
  @include font-main;

  // background-color: #ebebeb;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  background: none;
  border: none;

  &:focus {
  }
}

p + p {
  margin-top: 15px;
}

ul {
  list-style: none;
}

.ibg {
  position: relative;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.ibg img {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  visibility: hidden;
  opacity: 0;
}

h1,
h2,
h3,
h4 {
  padding: 0;
  margin: 0;
}

input {
  &::placeholder {
    @include anim;
  }

  &:focus {
    &::placeholder {
      opacity: 0;
    }
  }
}
