.App{max-width:500px;margin:0 auto}

.Screen{height:20vh;padding:0 30px;font-size:50px;display:flex;align-items:center;border:1px solid #47b5ff}

.Grid{display:flex;flex-wrap:wrap;min-height:80vh}.button-wrapper{flex:0 0 25%}

.Button{width:100%;height:100%;font-size:50px;color:black;text-transform:uppercase;cursor:pointer;transition:0.2s;border:1px solid #46b5ff}.Button:disabled{background-color:#a1a1a1}.Button:disabled:hover{cursor:not-allowed}.Button:hover:not(:disabled){opacity:0.8}.error{background-color:#eb6767}.primary{background-color:#67dceb}.success{background-color:#67eb67}

